/* General Styling */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

html, body {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    background: white; /* Keep background white */
    color: black; /* Adjust text color for contrast */
}

/* Navbar (Top) - Logo Left, Text Next to It */
.navbar {
    background: linear-gradient(135deg, #d71920, #132577); /* Apply gradient */
    padding: 10px;
}

.navbar-brand {
    display: flex;
    align-items: center;
    color: white;
}

.logo {
    height: 50px;
    margin-right: 10px;
}

.brand-text h2 {
    font-size: 1.2rem;
    margin: 0;
}

.brand-text h6 {
    font-size: 0.7rem;
    margin: 0;
}

/* Home Container */
.home-container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x: hidden;
}

/* Top Red Block */
.top-red-block {
    background: linear-gradient(135deg, #d71920, #132577);
    color: white;
    text-align: center;
    padding: 20px;
    width: 100%;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.top-red-block img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-bottom: 10px;
}

.top-red-block h2 {
    font-size: 1.5rem;
    margin: 0;
}

.top-red-block h6 {
    font-size: 0.9rem;
    margin: 5px 0 0;
}

/* Tabs */
.tabs-container {
    display: flex;
    justify-content: center;
    margin: 20px 0;
    width: 100%;
}

.tab {
    padding: 10px 20px;
    margin: 0 10px;
    border: none;
    background: #ddd;
    cursor: pointer;
    border-radius: 5px;
    font-size: 16px;
    transition: background 0.3s ease;
}

.tab.active {
    background: linear-gradient(135deg, #d71920, #132577);
    color: white;
}

/* Posts */
.posts-container {
    width: 100%;
    max-width: 1000px; /* Make the post section wider */
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-y: auto; /* Enable scrolling */
    max-height: calc(100vh - 200px); /* Adjust height to fit within the viewport */
}

.post {
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
    width: 100%;
}

.post-image {
    width: 100%;
    height: auto;
    border-radius: 10px;
    margin-bottom: 10px;
}

.post-description {
    font-size: 16px;
    margin-bottom: 10px;
}

.post-actions {
    display: flex;
    justify-content: space-around;
    gap: 10px;
}

.like-btn {
    background: #f0f2f5;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    color: #1877f2; /* Facebook blue */
    display: flex;
    align-items: center;
    gap: 5px;
}

.like-btn.liked {
    background: #e7f3ff;
    color: #1877f2;
}

.share-btn {
    background: #f0f2f5;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
    color: #0088cc; /* Telegram blue */
    display: flex;
    align-items: center;
    gap: 5px;
}

.register-btn {
    background: linear-gradient(135deg, #d71920, #132577);
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 14px;
    cursor: pointer;
}

.register-btn:hover {
    background: linear-gradient(135deg, #b3151a, #0f1c5c);
}

/* Post Section */
.post-container {
    display: none;
    flex-direction: column;
    align-items: center;
    padding: 15px;
    max-width: 100%;
    margin: 0 auto;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.5s ease, transform 0.5s ease;
}

.active-tab {
    display: flex !important;
    opacity: 1;
    transform: translateY(0);
}

/* Post Card */
.post {
    background: white;
    width: 100%;
    margin-bottom: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    padding: 12px;
    text-align: left;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.post:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 12px rgba(0,0,0,0.1);
}

/* Responsive Image Handling */
.post img {
    width: 100%;
    height: auto;
    border-radius: 8px;
    display: block;
    object-fit: cover;
}

/* Post Actions */
.post-actions {
    display: flex;
    justify-content: space-between;
    margin-top: 8px;
}

/* Button Styling */
button {
    background: none;
    border: none;
    font-size: 16px;
    cursor: pointer;
}

/* Bottom Navbar (Black Background) */
.bottom-navbar {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #000;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 14px 0;
    border-radius: 15px 15px 0 0;
    box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.2);
}

.nav-item {
    text-align: center;
    flex: 1;
    transition: 0.3s;
    position: relative;
}

.nav-item i {
    font-size: 2rem; /* Increase icon size */
    color: #aaa;
    transition: 0.3s;
}

.nav-item span {
    display: block;
    font-size: 0.8rem;
    color: #ccc;
}

.nav-item.active i,
.nav-item.active span {
    color: white;
    font-weight: bold;
}

/* Add Explore Icon */
.nav-item.explore i {
    color: #ff9800; /* Orange for explore */
}

/* Center Icon */
.center-icon {
    position: absolute;
    top: -18px;
    background: #fff;
    padding: 12px;
    border-radius: 50%;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.center-icon i {
    color: black;
    font-size: 1.8rem;
}

/* Game Selection Popup */
.popup {
    display:block;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    text-align: center;
    width: 80%;
    max-width: 400px;
}

/* Game Selection Grid */
.game-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
    margin-top: 10px;
}

.game-box {
    padding: 15px;
    background: #eee;
    border-radius: 10px;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
}

.game-box:hover {
    background: #ddd;
}

/* Event Type Buttons */
.event-type-buttons {
    display: flex;
    justify-content: center;
    gap: 20px; /* Add spacing between buttons */
    margin: 20px 0;
}

.event-button {
    background: linear-gradient(135deg, #d71920, #132577);
    color: white;
    border: none;
    padding: 10px 30px; /* Adjust padding for better appearance */
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.event-button:hover {
    background: linear-gradient(135deg, #b3151a, #0f1c5c);
}

/* Form Styling */
.form-container {
    width: 90%;
    max-width: 600px;
    margin: 80px auto 50px;
    background: white;
    color: black;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.form-container h3 {
    text-align: center;
    margin-bottom: 15px;
    color: #333;
}

.form-container input,
.form-container select,
.form-container textarea {
    width: 100%;
    padding: 10px;
    border: 2px solid #ddd;
    border-radius: 8px;
    font-size: 16px;
    transition: 0.3s;
}

.form-container input:focus,
.form-container select:focus,
.form-container textarea:focus {
    border-color: #d71920;
    outline: none;
    transform: scale(1.02);
}

.form-container button {
    width: 100%;
    padding: 12px;
    background: linear-gradient(135deg, #d71920, #132577); /* Apply gradient */
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: 0.3s;
}

.form-container button:hover {
    background: linear-gradient(135deg, #b3151a, #0f1c5c); /* Darker shade for hover */
}

/* Adaptive Mobile Scaling */
@media (max-width: 480px) {
    .logo {
        height: 40px;
    }

    .brand-text h2 {
        font-size: 1rem;
    }

    .brand-text h6 {
        font-size: 0.6rem;
    }

    .tab {
        font-size: 12px;
        padding: 8px;
    }

    .post {
        padding: 8px;
    }

    .post-actions {
        font-size: 12px;
    }

    .bottom-navbar {
        padding: 10px 0;
    }

    .nav-item i {
        font-size: 1.3rem;
    }

    .nav-item span {
        font-size: 0.7rem;
    }

    .center-icon i {
        font-size: 1.6rem;
    }
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
    .tabs-container {
        flex-direction: column;
        align-items: center;
    }

    .tab {
        margin: 5px 0;
        width: 90%;
        text-align: center;
    }

    .posts-container {
        padding: 10px;
    }

    .post {
        padding: 15px;
    }

    .post-description {
        font-size: 14px;
    }

    .post-actions {
        flex-direction: column;
        gap: 5px;
    }

    .like-btn,
    .share-btn,
    .register-btn {
        width: 100%;
        padding: 10px;
    }
}




