.feed-container {
    text-align: center;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    overflow-y: auto; /* Enable scrolling */
    height: 100vh;
    box-sizing: border-box;
}

.top-red-block {
    background: linear-gradient(135deg, #d71920, #132577);
    color: white;
    text-align: center;
    padding: 20px;
    width: 100%;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
}

.top-red-block h2 {
    font-size: 1.5rem;
    margin: 0;
}

.top-red-block h6 {
    font-size: 0.9rem;
    margin: 5px 0 0;
}

.button-section {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    width: 100%;
}

.feed-button {
    background: linear-gradient(135deg, #d71920, #132577);
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 8px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    flex: 1;
    max-width: 200px;
}

.feed-button:hover {
    background: linear-gradient(135deg, #b3151a, #0f1c5c);
}

.static-content {
    background: white;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    width: 100%;
    text-align: left;
}

.static-content h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
}

.static-content p {
    font-size: 1rem;
    line-height: 1.5;
    color: #333;
}
